
import XHeader from 'vux/src/components/x-header/index.vue'

import { TabContainer, TabContainerItem,LoadMore,Popup,Toast, MessageBox } from "mint-ui";
import "@/assets/css/main.css";
export default {
name: "order",
data() {
return {
users: localStorage.getItem('users') ? JSON.parse(localStorage.getItem('users')) : '',
title: "我的产品",
active: "sq_content",
timeout:'',
timeout2:'',
popupVisible:false,
reloadState:1,
loading:false,
isr:true,
arrow:true,
datalist:[],
datalists:[],
page:1,
pages:1,
allLoaded:false,
topStatus:'',
topRefreshTime:'',
topRefreshval:'',
bottomShow:'',
};
},
components: {
XHeader,
TabContainer,
TabContainerItem,
LoadMore
},
mounted(){
this.getlist();
console.log(this.topStatus)
},
methods: {
post_handle(){
this.$router.push("/loan/push-product");
},
tabBtnClick(type) {
this.active = type;
if(type=="sq_content"){
this.getlist();
}else if(type=="sh_content"){
this.togetlist();
}
},
loadBottom(){
this.page=this.page+1;
this.getlist();
this.loading = true;
this.timeout=setTimeout(() => {
this.$refs.loadmore.onBottomLoaded();
}, 1500);
},
loadTop(){
this.page=1;
this.getlist();

this.timeout2=setTimeout(()=>{
this.$refs.loadmore.onTopLoaded();

},500);
},
handleTopChange(status){
this.topStatus=status;
if(this.topStatus=="drop"){
this.topRefreshval="松开立即刷新";
}else if(this.topStatus=="pull"){
this.topRefreshval="下拉可以刷新";
}else{
this.topRefreshval="正在刷新数据中...";
}
},
handleBottomChange(status){
this.bottomShow=status;
},
getlist(){
this.$axios.post('/api/Lenders_product/product_list',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
page:this.page,
status:1,
})}`).then((res)=>{
if(res.code==200){
this.isHaveMore(res.data.list);
this.$nextTick(function () {

if(this.allLoaded===false)
this.datalist=res.data.list;
else Toast("暂无更多数据");
})

}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},
togetlist(){
this.$axios.post('/api/Lenders_product/product_list',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
page:this.pages,
status:0,
})}`).then((res)=>{
if(res.code==200){
this.datalists=res.data.list;

}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},
totop(id){
this.$axios.post('/api/Lenders_product/product_top',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
product_id:id
})}`).then((res)=>{
if(res.code==200){

this.getlist();
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},
totop2(id){
this.$axios.post('/api/Lenders_product/product_top',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
product_id:id
})}`).then((res)=>{
if(res.code==200){

this.togetlist();
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});
},
delgood(id){//删除产品
MessageBox.confirm('确认删除该产品吗?').then(action => {
this.$axios.post('/api/Lenders_product/product_delete',`data=${JSON.stringify({
session_id:this.users.session_id,
userId:this.users.userId,
product_id:id
})}`).then((res)=>{
if(res.code==200){
Toast(res.msg)
this.getlist();
}else{
Toast(res.msg)
}
}).catch((err)=>{throw err});

});
},
buyclick(id){
this.$router.push({path:'/loan/click-buy',query:{id:id}})
},
isHaveMore(isHaveMore){


if(Object.keys(isHaveMore).length === 0){
this.allLoaded = true;
} else{
this.allLoaded = false;
}
},
},
beforeDestroy () {
if(this.timeout){
clearTimeout(this.timeout)
};
if (this.timeout2){
clearTimeout(this.timeout2)
}
},



};



